import md5 from './md5';
import { getFromCache, saveToCache } from './cache-browser';

let uniqueIds = [];

async function isUnique(uniqueConfig, extendedEventBody){
  const stringifiedEvent = JSON.stringify(extendedEventBody);
  const hash = uniqueConfig.hashingMethod === 'md5' ? md5(stringifiedEvent) : stringifiedEvent;
  const expiryTime = uniqueConfig.maxAge ? (Date.now() + uniqueConfig.maxAge) : undefined;
  const item = {
    hash,
    expiryTime
  };

  if (uniqueConfig.storage && uniqueConfig.storage === 'indexeddb') {
    const alreadySentEvent = await getFromCache(hash, uniqueConfig);
    if (alreadySentEvent) {
      return false;
    }
    saveToCache(hash, uniqueConfig);
    return true;
  }

  const alreadySentEvent = uniqueIds.find(item => item.hash === hash);
  if (alreadySentEvent) {
    if (alreadySentEvent.expiryTime && alreadySentEvent.expiryTime < Date.now()) {
      uniqueIds = uniqueIds.filter(item => item.hash !== hash);
    } else {
      return false;
    }
  }
  uniqueIds.push(item);
  return true;
}

export default isUnique;
